@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap";
*, :root {
  --text-red-warn: #ff001b;
  --text-orange: #ff6b08;
  --text-black: #000;
  --text-white: #fff;
  --text-footer: #545454;
  --bg-white: #fff;
  --bg-gray: #e5e5e5;
}

:after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
  font-family: Roboto, sans-serif;
  font-size: 100%;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 1rem;
}

.section {
  padding-top: 94px;
  padding-bottom: 94px;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

@media screen and (min-width: 768px) {
  .container {
    min-width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    min-width: 1200px;
  }
}

.posters {
  max-width: 320px;
  width: 100%;
  flex-direction: column;
  gap: 1.25rem;
  margin-block: 1.25rem;
  margin-inline: auto;
  padding-inline: 1.25rem;
  display: flex;
}

.posters__box {
  font-size: .75rem;
  font-weight: 500;
  line-height: 1rem;
}

.posters__box:hover {
  cursor: pointer;
}

.posters__box:focus {
  border-radius: 5px;
  outline: 3px solid #ff6b08;
}

.posters__img {
  width: 100%;
  object-fit: cover;
  border-radius: 5px;
  margin-bottom: .625rem;
}

.posters__title {
  text-transform: uppercase;
}

.posters__details {
  color: #ff6b08;
}

.posters__ranking {
  text-align: center;
  color: #fff;
  width: 2.25rem;
  height: 1rem;
  background-color: #ff6b08;
  border-radius: 5px;
  margin-left: .25rem;
  font-size: .75rem;
  line-height: .875rem;
  display: inline-block;
}

@media screen and (min-width: 768px) {
  .posters {
    max-width: 768px;
    flex-flow: wrap;
    gap: 2rem;
    margin-block: 3.75rem;
    padding-inline: 2rem;
  }

  .posters__box {
    max-width: 336px;
    flex-grow: 1;
  }

  .posters__img {
    max-height: 455px;
  }
}

@media screen and (min-width: 1200px) {
  .posters {
    max-width: 1280px;
    gap: 1rem;
  }

  .posters__box {
    max-width: 395px;
    font-size: 1.25rem;
    line-height: 1.5rem;
  }

  .posters__img {
    max-height: 575px;
    margin-bottom: .75rem;
  }
}

